:root {
  --bg-color: #fffffe;
  --main-color: #6246ea;
  --sec-color: #d1d1e9;
  --tertiary-color: #e45858;
  --button-text: #fffffe;
  --text-color: #2b2c34;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  z-index: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--main-color);
  color: #212121;
  padding: 3em;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search {
  z-index: 1;
  -webkit-backdrop-filter: blur(5px);
  background: #ffffffe3;
  border: 1px solid #ffffff4d;
  border-radius: 16px;
  flex-direction: column;
  width: 40vw;
  max-height: 90vh;
  padding: 4em 3em;
  transition: all 1s cubic-bezier(.455, .03, .515, .955);
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 30px #0000001a;
}

.search > * {
  transition: all 1s cubic-bezier(.165, .84, .44, 1);
}

.search__box {
  border: none;
  border-bottom: 2px solid var(--main-color);
  color: var(--text-color);
  font-size: 1em;
  font: inherit;
  text-align: center;
  background: none;
  outline: none;
  padding: 0 .7em .6em;
  position: relative;
}

.country-list {
  z-index: 2;
  border-inline: var(--main-color) 1px solid;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: #f0f0f9cc;
  width: 90%;
  max-height: 10em;
  list-style: none;
  position: absolute;
  top: 6rem;
  overflow-y: auto;
  box-shadow: inset -200px 200px 7px -200px #000000bf;
}

.country-list__item {
  border-bottom: var(--sec-color) 1px solid;
  cursor: pointer;
  gap: 2em;
  padding: .3em;
  font-size: 1.2em;
  display: flex;
}

.country-list__item:hover {
  background-color: var(--sec-color);
}

.country-list__item:last-child {
  border-bottom-color: var(--main-color);
}

.country-info__symbols {
  justify-content: center;
  gap: 7em;
  margin-block: 2em;
  display: flex;
}

.country-info__flag, .country-info__emblem {
  max-width: 15em;
  max-height: 10em;
}

.country-info__common-name {
  color: var(--main-color);
  text-align: center;
  letter-spacing: .1em;
  margin-block: .5em;
  font-size: 2em;
}

.country-info__data__wrapper {
  align-items: baseline;
  gap: 1em;
  margin-bottom: .2em;
  display: flex;
}

.country-info__data__separator {
  border-bottom: var(--sec-color) 2px double;
  width: 50%;
  margin-top: .3em;
  margin-bottom: .5em;
}

.country-info__maps-google, .country-info__maps-google {
  cursor: pointer;
  max-width: 4rem;
  margin: 1rem 0 0 1rem;
}

@media (max-width: 1240px) {
  .search {
    width: 80vw;
  }
}

@media (max-width: 570px) {
  .search {
    width: 95vw;
    overflow-y: auto;
  }

  .country-list {
    width: 80%;
  }

  .country-info__symbols {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
  }

  .country-info__emblem {
    max-width: 5rem;
  }

  .country-info__data__wrapper {
    flex-direction: column;
    gap: .1rem;
  }
}

